#tttconnect {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: fit-content;
    min-height: 100%;
}

#tttconnect #cctop {
    position: fixed;
    top: 80px;
    left: 0px;

    z-index: 200;

    display: flex;
    justify-content: space-evenly;
    align-items: center;

    flex-direction: column;

    width: 70px;
    height: calc(100% - 80px);

    background-color: black;
    /* border-right: 1px solid #201F20; */
    /* border-bottom: 1px solid white; */
}

#tttconnect #cctop .ccoption {
    cursor: pointer;

    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    text-align: center;
    
    width: 100%;
    height: 100%;

    background-color: white;
}
#tttconnect #cctop .ccoption label {
    cursor: pointer;

    font-size: 18px;
    font-weight: 700;

    color: black;

    white-space: nowrap;
    transform: rotate(-90deg);

    transition: -webkit-text-stroke .2s;
}
#tttconnect #cctop .ccoption:hover {
    filter: brightness(.8);
}

#tttconnect #cctop .ccoption.active {
    background-color: black;
}
#tttconnect #cctop .ccoption.active label {
    color: white;
}
#tttconnect #cctop .ccoption.active svg {
    visibility: hidden;
}
#tttconnect #cctop .ccoption.active:hover {
    filter: brightness(1);
}

#tttconnect #cctleft {
    position: relative;

}
#tttconnect #cctright {
    position: relative;

}



#tttconnect #ccbottom {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: fit-content;
    min-height: calc(100vh - 80px);

    padding-left: 70px;
}


#tttwait {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    text-align: center;

    padding: 0px 20vw;
}
