#twcont {
    display: flex;
    justify-content: center;
    align-items: center;

    width: fit-content;
    height: fit-content;
}

#twleft {
    width: fit-content;
    height: fit-content;
}

#twleft #drawcont {
    overflow: hidden;

    width: 450px;
    height: fit-content;

    /* border: 2px solid white; */
    background-color: white;
    border-radius: 3px;

    transition: height .2s;
}
#twleft #drawshowcase {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    color: black;
    font-size: 18px;
    font-weight: 700;

    width: 100%;
    height: 80px;
}
#twleft #drawplace {
    width: 100%;
    height: 240px;

    padding: 10px 40px;
    padding-bottom: 30px;
}


#mobilemodebar {
    z-index: 200;

    display: flex;
    justify-content: space-evenly;
    align-items: center;

    position: fixed;
    bottom: 0px;

    width: 100vw;
    height: 70px;

    background-color: white;
    color: black;

    display: none !important;
    /* background-color: red; */
}
#mobilemodebar .mmboption {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    font-size: 17px;

    background-color: white;
}
#mobilemodebar .mmboption.active {
    background-color: black;
    color: white;
}
#mobilemodebar .mmboption:hover {
    filter: brightness(.8);
}
#mobilemodebar .mmboption.active:hover {
    filter: brightness(1);
}

