#tttplay {
    display: flex;
    justify-content: center;
    align-items: center;

    flex-direction: column;

    width: 100%;
    height: fit-content;
    min-height: 100%;

    /* padding-top: 20px; */
}


#board {
    z-index: 1;

    display: flex;
    flex-wrap: wrap;

    /* width: calc(3 * 160px);
    height: calc(3 * 160px); */

    width: calc(100vh / 1.57);
    height: calc(100vh / 1.57);

    max-width: calc(100vw - 40px);
    max-height: calc(100vw - 40px);

    outline: 3px solid white;
}

.cell {
    cursor: pointer;

    position: relative;

    width: 160px;
    height: 160px;

    width: calc((100vh / 1.57) / 3);
    height: calc((100vh / 1.57) / 3);

    max-width: calc((100vw - 40px) / 3);
    max-height: calc((100vw - 40px) / 3);

    background-color: black;
    border: 2px solid #202020;
    /* outline: 2px solid #202020; */
}
.cell .cellShape {
    position: absolute;
    top: 0px;
    z-index: 20;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    color: white;
    font-size: 80px;
    font-weight: 900;
}
.cell .cellShape img {
    max-width: 70%;

    /* max-width: 50%;
    max-height: 50%;

    min-width: 50%;
    min-height: 50%; */
}

.cell .cellHover {
    display: flex;
    justify-content: center;
    align-items: center;

    visibility: hidden;

    width: 100%;
    height: 100%;

    color: white;
    font-size: 80px;
    font-weight: 900;

    opacity: .3;
}
.cell .cellHover img {
    max-width: 70%
    
    /* max-width: 50%;
    max-height: 50%;

    min-width: 50%;
    min-height: 50%; */
}
.cell .cellHover.hover {
    visibility: visible;
}

/* .cell:nth-of-type(1) {
    outline: 0px;
} */



#tttplay .player {
    z-index: 2;

    display: flex;
    justify-content: space-between;
    align-items: center;

    width: calc(100vh / 1.57);
    /* width: calc(3 * 160px + 2px); */
    height: calc(100vh / 10.8);

    max-width: calc(100vw - 40px);
    max-height: calc((100vw - 40px) / 6.85);


    color: white;
    font-size: 16px;
    font-weight: 600;

    font-size: calc(100vh / 45);
    /* font-size: max(calc(100vw - 40px) / 28); */

    padding: 0px 20px;
    padding-left: 30px;
    padding-right: 0px;
    /* padding-bottom: 2px; */

    /* background-color: white; */
    /* background-color: white; */
    /* border: 2px solid white; */
    outline: 3px solid white;
    border-radius: 5px 5px 0px 0px;

    background-color: black;
    /* border-bottom: 0px; */

    transition: background-color .2s, border-color .2s, color .2s;
}
#tttplay .player:last-of-type {
    border-radius: 0px 0px 5px 5px;
    padding-bottom: 0px;
    /* padding-top: 2px; */
}
#tttplay .player div {
    display: flex;
    justify-content: center;
    align-items: center;

    width: calc(100vh / 10.8 + 10px);
    /* width: 100px; */
    /* height: 50px; */
    height: 100%;

    background-color: black;
    /* border: 2px solid white; */
    border-left: 3px solid white;
    /* border-radius: 7px; */

    transition: border-color .2s;
}
#tttplay .player img {
    /* transform: rotate(180deg); */
    max-height: 50%;
}

#tttplay .player.active {
    color: black;
    background-color: white;
    border-color: white;
}
#tttplay .player.active div {
    border-color: black;
}


#gamestats {
    position: relative;

    display: flex;
    flex-direction: column;
    /* justify-content: space-between;
    align-items: center; */

    width: 300px;
    height: calc(3 * 180px);

    /* background-color: white; */

    margin-left: 40px;
}

#gamestats .player {
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;
    height: fit-content;

    color: white;
    font-size: 16px;
    font-weight: 600;

    padding: 10px 20px;
    padding-left: 30px;

    /* background-color: white; */
    /* background-color: white; */
    border: 3px solid white;
    border-radius: 5px;

    transition: background-color .2s, border-color .2s, color .2s;
}
#gamestats .player div {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100px;
    height: 50px;

    background-color: black;
    border: 3px solid white;
    border-radius: 7px;

    transition: border-color .2s;
}
#gamestats .player img {
    /* transform: rotate(180deg); */
    height: 30px;
}

#gamestats .player.active {
    color: black;
    background-color: white;
    border-color: white;
}
#gamestats .player.active div {
    border-color: black;
}

#gamestats #gsdesc {
    width: 100%;
    height: 100%;

    margin: 15px 0px;

    background-color: black;
    border-radius: 5px;
    border: 3px solid white;
}


#rescont {
    z-index: 200;

    position: fixed;
    top: 0px;
    left: 70px;

    display: none;
    justify-content: center;
    align-items: center;

    width: calc(100% - 70px);
    height: calc(100% - 5px);

    /* background-color: #00000030; */
}

#res {
    /* display: flex;
    justify-content: center;
    align-items: center; */

    width: fit-content;
    height: fit-content;

    background-color: white;
    border-radius: 10px;
    border: 3px solid black;

    padding: 5px 35px;
    padding-bottom: 25px;
}
#res label {
    display: block;
    text-align: center;
}
#res #resres {
    color: black;
    font-size: 150px;
    font-weight: 900;
}
#res button {
    display: block;

    width: 100%;
    height: 50px;

    margin-bottom: 15px;
}





#tttplay #cpresultcont {
    z-index: 301;

    position: fixed;
    top: 0px;
    left: 0px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100vw;
    height: 100%;

    background-color: #20202090;
}

#tttplay #cpresultcont #cprescontrol {
    width: 460px;
    height: fit-content;

    background-color: white;
    border-radius: 10px;

    padding: 50px 40px;
}
#tttplay #cpresultcont #cprescontrol label {
    display: block;

    text-align: center;

    color: black;
    font-size: 50px;
    font-weight: 800;

    margin-bottom: 50px;
}
#tttplay #cpresultcont #cprescontrol label span {
    color: #0029FF;
    font-size: 50px;
    font-weight: 800;
}
#tttplay #cpresultcont #cprescontrol button {
    cursor: pointer;

    width: 100%;
    height: 60px;

    color: white;
    font-size: 15px;
    font-weight: 700;

    border: none;
    border-radius: 5px;

    outline: 4px solid transparent;

    transition: outline .1s;
}
#tttplay #cpresultcont #cprescontrol #pabtn {
    background: linear-gradient(97.73deg, #001FC4 0%, #3F5EFD 100%);
}
#tttplay #cpresultcont #cprescontrol #pabtn:hover {
    outline: 4px solid #FFF500;
}
#tttplay #cpresultcont #cprescontrol #gtmbtn {
    background-color: black;
    margin-top: 20px;
}
#tttplay #cpresultcont #cprescontrol #gtmbtn:hover {
    outline: 4px solid #FFF500;
}


#ttt.mobile {
    padding-top: 80px;
    padding-bottom: 0px;
    /* padding-bottom: 10px; */
}
#ttt.mobile #cctop {
    display: none;
}
#ttt.mobile #ccbottom {
    /* padding: 0px; */
    padding-left: 0px;
    padding-top: 40px !important;
    padding-bottom: 120px !important;
}
#mobilemodebar {
    display: flex;
}

#ttt.mobile .player {
    font-size: max(calc(100vw - 40px) / 28);
}

#ttt.mid #ccbottom {
    padding-top: 40px;
    padding-bottom: 40px;
}

@media screen and (max-width: 620px) {
    #ttt {
        /* padding-bottom: 10px; */
        padding-bottom: 0px;
    }
    #ttt #cctop {
        display: none;
    }
    #ttt #ccbottom {
        /* padding: 0px; */
        padding-left: 0px;
        padding-bottom: 120px;
    }
    #mobilemodebar {
        display: flex;
    }

    #ttt .player {
        font-size: max(calc(100vw - 40px) / 28);
    }

}

