#gamecontrolcont {
    width: fit-content;
    height: fit-content;

    margin-right: 100px;
}

#gamecontrolcont #drawcont {
    overflow: hidden;

    width: calc(100vw - 40px);
    max-width: 450px;
    height: 80px;

    /* border: 2px solid white; */
    background-color: white;
    border-radius: 3px;

    transition: height .2s;
}

#gamecontrolcont #drawshowcase {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    color: black;
    font-size: 18px;
    font-weight: 700;

    width: 100%;
    height: 80px;
}
#gamecontrolcont #drawshowcase #dsdropdown {
    cursor: pointer;

    position: absolute;
    right: 0px;

    display: flex;
    justify-content: center;
    align-items: center;

    height: 100%;
    width: 80px;
}
#gamecontrolcont #drawshowcase #dsdropdown img {
    transition: transform .2s;
}
#gamecontrolcont #drawshowcase #dsdropdown:hover {
    background-color: #f5f5f5;
}

#gamecontrolcont #drawplace {
    display: flex;
    justify-content: center;
    align-items: center;

    flex-direction: column;

    width: 100%;
    height: fit-content;

    padding: 10px 40px;
    padding-bottom: 30px;
}


#gamecontrolcont #colorcont {
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    width: calc(100vw - 40px);
    max-width: 450px;
    height: 80px;

    margin: 20px 0px;
}
#gamecontrolcont #colorcont .colorbtn {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    color: white;
    font-size: 20px;

    border: 3px solid white;
    border-radius: 5px;

    transition: border .1s, background-color .1s;
}
#gamecontrolcont #colorcont .colorbtn img {
    cursor: pointer;

    max-height: 50%;
    
    transition: color .1s;
}
#gamecontrolcont #colorcont .colorbtn:hover img {
    filter: brightness(0) saturate(100%) invert(76%) sepia(87%) saturate(1794%) hue-rotate(10deg) brightness(117%) contrast(106%);
}


#gamecontrolcont #colorcont .colorbtn.active {
    color: black;
    background-color: #FFF500;
    border: 3px solid #FFF500;
}
#gamecontrolcont #colorcont .colorbtn.active img {
    filter: brightness(0) saturate(100%);
}


#gamecontrolcont #playbutton {
    cursor: not-allowed;

    width: calc(100vw - 40px);
    max-width: 450px;
    height: 90px;

    color: white;
    font-size: 25px;
    font-weight: 700;

    background: linear-gradient(97.73deg, #001FC4 0%, #3F5EFD 100%);
    border-radius: 7px;
    border: none;

    opacity: .3;

    outline: 4px solid transparent;
    transition: background-color .2s, opacity .2s, outline .1s;
}
#gamecontrolcont #playbutton.ready {
    cursor: pointer;
    opacity: 1;
}
#gamecontrolcont #playbutton.ready:hover {
    outline: 4px solid white;
}




