#chessboard {
    /* position: relative; */

    display: flex;
    flex-wrap: wrap;

    width: calc(100vh - 160px);
    height: calc(100vh - 160px);

    max-width: calc(100vw - 100px);
    max-height: calc(100vw - 100px);

    background-color: white;
}
#chessboard .square {
    /* position: relative; */

    display: flex;
    justify-content: center;
    align-items: center;

    width: calc((100vh - 160px) / 8);
    height: calc((100vh - 160px) / 8);

    max-width: calc((100vw - 100px) / 8);
    max-height: calc((100vw - 100px) / 8);

    /* transition: all .2s; */
    transition: box-shadow .1s;
}
#chessboard .square .piece {
    z-index: 3;

    width: calc((100vh - 160px) / 8);
    height: calc((100vh - 160px) / 8);

    max-width: calc((100vw - 100px) / 8);
    max-height: calc((100vw - 100px) / 8);

    background-repeat: no-repeat;
    background-position: center;
    /* background-size: 75%; */
    background-size: 65%;

    /* transition: all .2s; */
}

#chessboard .square .piece.pawn {
    background-size: 60%;
}
#chessboard .square .piece.bishop {
    background-size: 40%;
}

#chessboard .square .piece.you {
    cursor: grab;
}
#chessboard .square .piece.you:active {
    cursor: grabbing;
}

#chessboard .square .piece.opponent {
    cursor: not-allowed;
}
#chessboard .square .piece.opponent:active {
    cursor: not-allowed;
}


#chessboard .square.light {
    background-color: #C7E4FF;
}
#chessboard .square.dark {
    background-color: #001EBC;
}

#chessboard.gay .square.light {
    background-color: white;
}
#chessboard.gay .square.dark {
    background: linear-gradient(135.46deg, #FF0000 -0.37%, #FF9A00 9.22%, #D0DE21 18.95%, #4FDC4A 28.24%, #3FDAD8 38.12%, #2FC9E2 47.74%, #1C7FEE 57.76%, #5F15F2 67.1%, #BA0CF8 77.35%, #FB07D9 89.52%, #FF0000 100.27%);
}



#chessboard .square.hover {
    /* box-shadow: inset 0px 0px 10px 5px #FFFFFF; */
    z-index: 2;
    outline: 4px solid black;
    /* border-radius: 5px; */
}
#chessboard .square.checked {
    box-shadow: inset 0px 0px 10px 5px red;
    /* z-index: 2; */
    /* outline: 4px solid red;
    border-radius: 5px; */
}

#chessboard .square.selected {
    z-index: 2;
    outline: 4px solid black;
}