#chessconnect {
    display: flex;

    width: 100%;
    height: 100%;
}

#chessconnect #cctop {
    position: fixed;
    top: 0px;
    left: 0px;

    z-index: 200;

    display: flex;
    justify-content: space-evenly;
    align-items: center;

    flex-direction: column;

    width: 70px;
    height: calc(100vh);

    background-color: black;

    padding-top: 80px;
    /* border-right: 1px solid #201F20; */
    /* border-bottom: 1px solid white; */
}

#chessconnect #cctop .ccoption {
    cursor: pointer;

    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    text-align: center;
    
    width: 100%;
    height: 100%;

    background-color: white;
}
#chessconnect #cctop .ccoption label {
    cursor: pointer;

    font-size: 18px;
    font-weight: 700;

    color: black;

    white-space: nowrap;
    transform: rotate(-90deg);

    transition: -webkit-text-stroke .2s;
}
#chessconnect #cctop .ccoption:hover {
    filter: brightness(.8);
}

#chessconnect #cctop .ccoption.active {
    background-color: black;
}
#chessconnect #cctop .ccoption.active label {
    color: white;
}
#chessconnect #cctop .ccoption.active svg {
    visibility: hidden;
}
#chessconnect #cctop .ccoption.active:hover {
    filter: brightness(1);
}

#chessconnect #cctleft {
    position: relative;

}
#chessconnect #cctright {
    position: relative;

}


#chessconnect #ccbottomcont {
    display: flex;
    justify-content: center;
    align-items: center;    

    width: fit-content;
    height: fit-content;

    min-width: 100%;
    min-height: 100%;
}
#chessconnect #ccbottom {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: fit-content;
    min-height: calc(100vh - 80px);

    padding-left: 70px;
}


#chesswait {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    text-align: center;

    padding: 0px 20vw;
}
#chesswait h1 {
    font-size: calc(100vw / 20);
    font-weight: 700;

    color: white;
}
#chesswait button {
    cursor: pointer;

    width: calc(100% - 40px);
    max-width: 250px;
    height: 80px;

    color: white;
    font-size: 20px;
    font-weight: 700;

    background-color: #0029FF;
    border: none;
    border-radius: 4px;

    outline: 3px solid transparent;
    transition: background-color .2s, outline .1s;
}
#chesswait button:hover {
    background-color: #f00;
    outline: 3px solid white;
}


#mobilemodebar {
    z-index: 200;

    display: flex;
    justify-content: space-evenly;
    align-items: center;

    position: fixed;
    bottom: 0px;

    width: 100vw;
    height: 70px;

    background-color: white;
    color: black;

    display: none;
    /* background-color: red; */
}
#mobilemodebar .mmboption {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    font-size: 17px;

    background-color: white;
}
#mobilemodebar .mmboption.active {
    background-color: black;
    color: white;
}
#mobilemodebar .mmboption:hover {
    filter: brightness(.8);
}
#mobilemodebar .mmboption.active:hover {
    filter: brightness(1);
}

.dashedbordersep {
    display: block;

    position: absolute;
    top: 0px;
    left: 50%;
    transform: translate(-50%, 0px);
}
.dashedborderdown {
    display: block;

    position: absolute;
    bottom: -2px;
}


#chess.mobile #cctop {
    display: none;
}
#chess.mobile #ccbottom {
    padding-left: 0px;
}

#chess.mobile #mobilemodebar {
    display: flex;
}




@media screen and (max-width: 1150px) {
    
    #chessconnect #ccbottomcont {
        padding-top: 150px;
        padding-bottom: 50px;
    }
    #chessconnect #ccbottom {
        flex-direction: column;

        padding: 50px 0px;
        padding-bottom: calc(70px + 50px);
        padding-left: 70px;
    }

    #chessconnect #chessinvitecont {
        margin-top: 100px;
    }
    #chessconnect #gamecontrolcont {
        margin-right: 0px;
    }
    
}

@media screen and (max-width: 620px) {
    #chess #cctop {
        display: none;
    }
    #mobilemodebar {
        display: flex;
    }
}

