#chessplay {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: fit-content;
    min-height: 100%;
}

#chessplay #cpresultcont {
    z-index: 301;

    position: fixed;
    top: 0px;
    left: 0px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100vw;
    height: 100%;

    background-color: #20202090;
}

#chessplay #cpresultcont #cprescontrol {
    width: 460px;
    height: fit-content;

    background-color: white;
    border-radius: 10px;

    padding: 50px 40px;
}
#chessplay #cpresultcont #cprescontrol label {
    display: block;

    text-align: center;

    color: black;
    font-size: 50px;
    font-weight: 800;

    margin-bottom: 50px;
}
#chessplay #cpresultcont #cprescontrol label span {
    color: #0029FF;
    font-size: 50px;
    font-weight: 800;
}
#chessplay #cpresultcont #cprescontrol button {
    cursor: pointer;

    width: 100%;
    height: 60px;

    color: white;
    font-size: 15px;
    font-weight: 700;

    border: none;
    border-radius: 5px;

    outline: 4px solid transparent;

    transition: outline .1s;
}
#chessplay #cpresultcont #cprescontrol #pabtn {
    background: linear-gradient(97.73deg, #001FC4 0%, #3F5EFD 100%);
}
#chessplay #cpresultcont #cprescontrol #pabtn:hover {
    outline: 4px solid #FFF500;
}
#chessplay #cpresultcont #cprescontrol #gtmbtn {
    background-color: black;
    margin-top: 20px;
}
#chessplay #cpresultcont #cprescontrol #gtmbtn:hover {
    outline: 4px solid #FFF500;
}

