#chessplay #cppawncont {
    z-index: 300;

    position: fixed;
    top: 0px;
    left: 0px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100vw;
    height: calc(100vh - 80px);

    margin-top: 80px;

    background-color: #20202090;
}
#chessplay #cppawncont #cppawn {
    display: flex;

    overflow: hidden;

    width: fit-content;
    height: fit-content;

    background-color: white;
    padding: 0px;

    border-radius: 8px;
}
#chessplay #cppawncont .cppoption {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 160px;
    height: 160px;

    padding: 20px;
    /* margin-right: 30px; */

    transition: box-shadow .2s,  background-color .2s;
}
#chessplay #cppawncont .cppoption:last-of-type {
    margin-right: 0px;
}
#chessplay #cppawncont .cppoption img {
    max-width: 80%;
    max-height: 80%;

    min-height: 80%;
    min-width: 80%;
}
#chessplay #cppawncont .cppoption:hover {
    /* -webkit-box-shadow: 0px 0px 15px 5px #000000; 
    box-shadow: 0px 0px 15px 5px #202020; */

    background-color: #f5f5f5;
    background-color: #FFF500;
}

#chessplay #cpdrawcont {
    z-index: 300;

    position: fixed;
    top: 0px;
    left: 0px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100vw;
    height: calc(100vh - 80px);

    margin-top: 80px;

    background-color: #20202090;
}
#chessplay #cpdrawcont #cpdraw {
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    flex-direction: column;

    width: 460px;
    height: fit-content;

    background-color: white;
    border-radius: 10px;

    padding: 20px 30px;
}
#chessplay #cpdrawcont #cpdraw #cpdrawbtncont {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: fit-content;
}
#chessplay #cpdrawcont #cpdraw button {
    cursor: pointer;

    width: 100%;
    height: 50px;

    color: white;
    font-size: 16px;

    border: none;
    border-radius: 5px;

    outline: 4px solid transparent;
    transition: outline-color .2s;
}
#chessplay #cpdrawcont #cpdraw button:first-of-type {
    margin-right: 10px;
    background-color: #0029FF;
}
#chessplay #cpdrawcont #cpdraw button:last-of-type {
    margin-left: 10px;
    background-color: black;
}
#chessplay #cpdrawcont #cpdraw button:hover {
    outline-color: #FFF500;
}

#chessplay #cpleft {
    display: flex;
    justify-content: center;
    align-items: center;

    width: fit-content;
    height: 100%;
}


