#chessplay #cpmid {
    position: relative;

    width: 80px;
    height: 100vh;
}

#chessplay #cpmid #cparrow {
    position: absolute;
    top: 0px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 80px;
    height: 80px;

    transition: top .2s;
}


#cpright {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    flex-direction: column;

    min-width: calc((100vh - 160px) / 1.875);
    height: calc(100vh - 160px);

    margin: 0px 0px;

    /* background-color: white; */
    /* border: 2px solid red; */

    margin-left: 50px;
}

#cpright .player, .player {
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;
    height: 70px;

    color: white;
    font-size: 15px;
    font-weight: 700;

    outline: 2px solid white;
    border-radius: 5px;

    padding: 10px 20px;
    padding-left: 25px;

    transition: color .2s, background-color .2s;
}
#cpright .player .time, .player .time {
    display: flex;
    justify-content: center;
    align-items: center;

    width: fit-content;
    min-width: 140px;
    height: 100%;

    border: 2px solid white;
    border-radius: 5px;

    padding: 10px 50px;

    transition: color .2s, background-color .2s;
}

#cpright .player.active, .player.active {
    color: black;
    background-color: white;
    /* background-color: #FFF500; */
    /* outline-color: #FFF500; */
}
#cpright .player.active .time, .player.active .time {
    color: white;
    background-color: black;
    /* border-color: white; */
    /* border-color: #FFF500; */
}

#cpright #cpcontrol {
    position: relative;
    
    display: flex;
    flex-direction: column;

    width: 100%;
    height: 100%;

    outline: 2px solid white;
    border-radius: 5px;

    margin: 20px 0px;

    overflow-y: auto;
}

#cpright #cpcontrol #cpctab {
    position: relative;

    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;
    height: 40px;
    min-height: 40px;

    border-bottom: 2px solid white;

    padding-left: 20px;
}
#cpright #cpcontrol #cpctab label {
    color: white;
}
#cpright #cpcontrol #cpctab #cpctsettings {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 40px;
    height: 100%;

    border-left: 2px solid white;
}
#cpright #cpcontrol #cpctab #cpctsettings.active {
    background-color: white;
}
#cpright #cpcontrol #cpctab #cpctsettings:hover {
    background-color: white;
    /* background: linear-gradient(
        90deg,
        rgba(255, 0, 0, 1) 0%,
        rgba(255, 154, 0, 1) 10%,
