        rgba(95, 21, 242, 1) 70%,
        rgba(186, 12, 248, 1) 80%,
        rgba(251, 7, 217, 1) 90%,
        rgba(255, 0, 0, 1) 100%
    ); */
}
#cpright #cpcontrol #cpctab #cpctsettings:hover img {
    filter: brightness(0) saturate(100%);
}

#cpright #cpcontrol #cpcmoves {
    position: relative;

    width: 100%;
    /* height: calc(100vh - 80px - 120px - 60px - 60px - 20px - 20px - 40px - 40px + 22px); */
    height: 100%;
    /* max-height: 100%; */

    overflow-y: auto;
}

#cpright #cpcontrol #cpcmoves table {
    color: white;
    
    overflow-y: auto;

    width: 100%;
    height: fit-content;

    border-collapse:collapse
}
#cpright #cpcontrol #cpcmoves table tr {
    height: 24px;
}
#cpright #cpcontrol #cpcmoves table tr:nth-child(odd) {
    background-color: #151515;
}
#cpright #cpcontrol #cpcmoves table tr:nth-child(even) {
    background-color: #202020;
}

#cpright #cpcontrol #cpcmoves table td {
    text-align: center;

    width: 20%;

    color: #FFF500;
    font-size: 13px;
    font-weight: 500;
}
#cpright #cpcontrol #cpcmoves table td:nth-of-type(2), #cpright #cpcontrol #cpcmoves table td:nth-of-type(3) {
    color: white;
    width: 40%;
}

#cpright #cpcontrol #cpcbtncont {
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    width: 100%;
    height: 40px;
    min-height: 40px;

    /* border-top: 2px solid white; */
    /* border-top: 2px solid white; */
}
#cpright #cpcontrol #cpcbtncont button {
    cursor: pointer;

    width: 100%;
    height: 100%;

    color: white;

    background-color: black;
    border: none;
    border-top: 2px solid white;

    transition: background-color .1s, color .1s;
}
#cpright #cpcontrol #cpcbtncont button:first-of-type {
    border-right: 2px solid white;
    width: 100%;
}
#cpright #cpcontrol #cpcbtncont button:hover {
    color: black;
    background-color: white;
}

#cpright #cpcontrol #cpcbtncont button.active {
    color: black;
    background-color: white;
}



