.gamesoption {
    width: 100%;
    height: fit-content;

    padding-top: 60px;
}

.gamesoption .goheader {
    cursor: pointer;

    display: flex;
    justify-content: flex-start;
    align-items: center;

    width: fit-content;
}
.gamesoption .goheader label {
    cursor: pointer;
    display: block;

    font-size: 30px;
    font-weight: 700;

    color: white;

    margin-right: 30px;

    user-select: none;

    transition: -webkit-text-stroke .2s;
}
.gamesoption .goheader:hover label {
    color: #FFF000;
}
.gamesoption .goheader img {
    max-height: 30px;

    transition: transform .2s;
}

.gamesoption .gogamescont {
    /* overflow-x: scroll;
    overflow-y: visible; */

    -webkit-overflow-scrolling: touch;
    /* display: flex;
    justify-content: flex-start;
    align-items: center; */
    
    white-space: nowrap;

    width: 100%;
    height: 225px;

    margin-top: 50px;
    padding-left: 15px;
    padding-top: 15px;

    transition: height .2s, padding .1s;
}
.gamesoption .gogamescont::-webkit-scrollbar {
    display: none;
}


.gamesoption .gogamescont .gogame {
    cursor: pointer;

    display: inline-block;

    position: relative;

    width: 350px;
    height: 210px;

    background-color: #202020;
    border-radius: 5px;

    margin-right: 60px;
}

@keyframes loading {
    0%{background-position:11% 0%}
    50%{background-position:90% 100%}
    100%{background-position:11% 0%}
}

.gamesoption .gogamescont .gogame div {
    position: absolute;
    right: 15px;
    bottom: 15px;

    width: 100%;
    height: 100%;

    transition: bottom .2s, right .2s, filter .2s;

    background: linear-gradient(321deg, #303030, #000000);
    background-size: 400% 400%;

    border-radius: 5px;

    animation: loading 1s ease infinite;
}
.gamesoption .gogamescont .gogame img {
    width: 100%;
    height: 100%;
    
    border-radius: 10px;
}
.gamesoption .gogamescont .gogame:hover {
    filter: brightness(.9);
}
.gamesoption .gogamescont .gogame:hover div {
    right: 0px;
    bottom: 0px;
}
