#grid {
    position: relative;

    /* width: calc(100vh - 80px - 2 * 60px); */
    width: fit-content;
    height: fit-content;

    overflow: hidden;

    outline: 0px solid transparent;

    border: 4px solid transparent;
    transition: border-radius .2s, border .2s;
}
#gridcont {
    position: relative;

    width: fit-content;
    height: fit-content;
}

.gridRow {
    display: flex;

    width: fit-content;
    height: fit-content;

}
.gridRow:nth-of-type(1) .square {
    border-top: 4px solid white;
}
.gridRow:nth-of-type(3n) .square {
    border-bottom: 4px solid white;
    box-shadow: 2px 0px 0px 0px #202020;
}


.gridRow .square {
    display: flex;
    justify-content: center;
    align-items: center;

    color: #0072e3;
    font-size: 30px;
    font-weight: 500;
    
    font-size: calc(((100vh - 80px - 2 * 60px - 10px) / 9) / 2);
    /* font-size: max(calc(((100vw - 50px) / 9) / 2)); */

    /* border-right: 1px solid #d8d8d8; */
    /* border-bottom: 1px solid #d8d8d8; */
    box-shadow: 0px 1px 0px 0px #202020, 1px 0px 0px 0px #202020;
    /* box-shadow: 1px 0 0px 0px #d8d8d8; */

    width: calc((100vh - 80px - 2 * 60px - 10px) / 9);
    height: calc((100vh - 80px - 2 * 60px - 10px) / 9);

    max-width: calc((100vw - 50px) / 9);
    max-height: calc((100vw - 50px) / 9);
    /* width: 60px;
    height: 60px; */

    /* transition: background-color .1s; */
}
.gridRow .square:nth-of-type(1) {
    border-left: 4px solid white;
}
.gridRow .square:nth-of-type(3n) {
    border-right: 4px solid white;
    box-shadow: 0px 1px 0px 0px #202020;
}

.gridRow .square.prefixed {
    color: white;
}

.gridRow .square.highlighted {
    background-color: #0072e330;
}

.gridRow .square.numlighted {
    background-color: #71b8ff40;
}

.gridRow .square.conflicted {
    background-color: #ff000030;
}
.gridRow .square.wrong {
    color: red;
}

.gridRow .square.sel {
    background-color: #0072e350;
}


#timelabel {
    position: absolute;
    left: 3px;
    top: -30px;

    top: calc(0 - ((100vh - 80px - 2 * 60px - 10px) / 9) / 2);

    font-size: 16px;
    color: white;

    font-size: calc(((100vh - 80px - 2 * 60px - 10px) / 9) / 3.75);

    transition: top .2s;
}
#difflabel {
    position: absolute;
    right: 3px;
    top: -30px;

    top: calc(0 - ((100vh - 80px - 2 * 60px - 10px) / 9) / 2);

    font-size: 16px;
    color: white;

    font-size: calc(((100vh - 80px - 2 * 60px - 10px) / 9) / 3.75);

    transition: top .2s;
}

#newgamelabel {
    display: none;

    position: absolute;
    left: 50%;
    top: -30px;
    transform: translate(-50%, -0px);

    top: calc(0 - ((100vh - 80px - 2 * 60px - 10px) / 9) / 2);

    font-size: 16px;
    color: white;

    font-size: calc(((100vh - 80px - 2 * 60px - 10px) / 9) / 3.75);

    transition: top .2s;
}



#gridsolved {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    position: absolute;
    top: 0px;
    left: 0px;

    /* position: relative; */

    z-index: 50;

    visibility: hidden;
    /* display: none; */

    width: 100%;
    height: 100%;

    background-position: center;
    background-size: cover;

    background-color: #0029FF;

    padding: 50px 50px;
    padding-top: 70px;

    transform: scale(.5);
    transition: transform .2s;
}
#gridsolved.solved {
    visibility: visible;
    transform: scale(1);

}
#gscont {
    display: flex;
    justify-content: center;
    align-items: center;

    flex-direction: column;
    position: relative;

    width: 100%;
    height: 100%;
}

#gridsolved #gstop {
    text-align: center;

    display: flex;
    flex-direction: column;
    position: relative;

    width: 100%;
    height: 100%;
}

#gridsolved #gstop #gsmainlabel {
    display: block;

    color: white;
    font-size: 25px;
    font-weight: 700;

    /* font-size: calc((100vh - 80px - 2 * 60px - 10px) / 22.2); */

    /* margin-bottom: 30px; */
}

#gridsolved #gstop #gsstatcont {
    display: flex;
    justify-content: center;
    align-items: center;

    flex-direction: column;

    width: 100%;
    height: 100%;
}
#gridsolved #gstop .gsstat {
    width: fit-content;
    height: fit-content;

    margin-top: 5px;
}
#gridsolved #gstop .gsstat label {
    color: white;
    font-size: 17px;
    font-weight: 700;
}
#gridsolved #gstop .gsstat label:first-of-type {
    margin-right: 40px;
}
#gridsolved #gstop .gsstat label:last-of-type {
    margin-left: 40px;
}


#gridsolved video {
    width: 100%;
    height: auto;

    border-radius: 20px;
    border: 4px solid white;
}

