#navbar {
    z-index: 1000;

    position: fixed;
    top: 0px;
    left: 0px;

    display: flex;
    justify-content: center;
    align-items: center;

    color: white;

    width: 100%;
    height: 80px;

    background-color: #050405;
    border-bottom: 1px solid #201F20;

    /* padding: 0px 200px; */
}

#navbar #nbcont {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 80%;
    height: 100%;
}

#navbar #nblogo {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: fit-content;
    height: 100%;

    color: #FFF500;
    font-size: 20px;
    font-weight: 800;
}
#navbar #nblogo #nbl {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    color: #FFF500;
}
#navbar #nblogo #nbburger {
    display: none;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;
}

#navbar #nbmenu {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;
}
#navbar #nbmenu #nbmenucont {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    /* top: 50%;
    left: 0%;
    transform: translate(0px, -50%); */

    display: flex;
    justify-content: center;
    align-items: center;

    width: fit-content;
    height: fit-content;
}
#navbar #nbmenu .nbmenuoption {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 110px;
    height: 100%;
}
#navbar #nbmenu .nbmenuoption label {
    cursor: pointer;

    color: white;
    font-size: 15px;
    font-weight: 700;

    padding: 0px 10px;
    padding-bottom: 10px;
    border-bottom: 3px solid transparent;

    margin-top: 13px;

    transition: border-bottom .1s;
}
#navbar #nbmenu .nbmenuoption:hover label {
    border-bottom: 3px solid white;
}

#navbar #nbmenu .nbmenuoption.active label {
    border-bottom: 3px solid #FFF500;
    color: #FFF500;
}

#navbar #nbaccount {
    display: flex;
    justify-content: center;
    align-items: center;

    width: fit-content;
    height: 100%;
}
#navbar #nbaccount button {
    cursor: pointer;

    color: white;
    font-size: 15px;
    font-weight: 700;

    width: 110px;
    height: 40px;
}

#navbar #nbaccount #loginbtn {
    background-color: transparent;
    border: none;
}
#navbar #nbaccount #loginbtn label {
    cursor: pointer;

    padding: 0px 10px;
    padding-bottom: 10px;
    border-bottom: 3px solid transparent;

    margin-top: 13px;

    transition: border-bottom .1s;
}
#navbar #nbaccount #loginbtn:hover label {
    border-bottom: 3px solid white;
}

#navbar #nbaccount #joinbtn {
    background: linear-gradient(97.73deg, #001FC4 0%, #3F5EFD 100%);
    border: none;
    border-radius: 3px;

    outline: 3px solid transparent;

    transition: outline .1s;
}
#navbar #nbaccount #joinbtn:hover {
    outline: 3px solid white;
}



#navbar.mid #nblogo #nbl {
    display: none !important;
}
#navbar.mid #nblogo #nbburger {
    display: flex !important;
}
#navbar.mid #nbmenu {
    display: none;
}

#navbar.mid {
    padding: 0px;
}
#navbar.mid #nbcont {
    justify-content: space-between !important;

    width: calc(100vh - 160px) !important;
    max-width: calc(100vw - 100px) !important;
}

#navbar.mobile #nblogo #nbl {
    display: none !important;
}
#navbar.mobile #nblogo #nbburger {
    display: flex !important;
}
#navbar.mobile #nbmenu {
    display: none;
}

#navbar.mobile {
    padding: 0px 0px;
    /* height: 50px; */
}
#navbar.mobile #nbcont {
    justify-content: space-between !important;

    width: calc(100vh - 40px) !important;
    max-width: calc(100vw - 40px) !important;
}


#navbar #nbburger:hover img {
    filter: brightness(0) saturate(100%) invert(74%) sepia(98%) saturate(438%) hue-rotate(2deg) brightness(109%) contrast(105%);
}

#navbar #nbdropdowncont {
    position: fixed;
    top: 80px;

    width: 100%;
    height: calc(100vh - 80px);

    background-color: #20202090;

    visibility: hidden;
}

#navbar #nbdropdown {
    display: flex;
    justify-content: center;

    flex-direction: column;

    width: 100%;
    height: fit-content;

    background-color: black;
    border-bottom: 2px solid #202020;
}

#navbar #nbdropdown .nbdoption {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: fit-content;

    font-size: 20px;

    color: white;
    padding: 30px 0px;

    background-color: black;
}
#navbar #nbdropdown .nbdoption:hover {
    /* color: #FFF500; */
    background-color: #FFF500;
    color: black !important;
}

#navbar #nbdropdown .nbdoption.active {
    color: #FFF500;
}

#navbar #nbdropdowncont.open {
    visibility: visible;
}

