#tttinvitecont {
    overflow-x: visible;
    overflow-y: hidden;

    width: calc(100vw - 40px);
    max-width: calc(450px + 2 * 4px);
    height: 390px;

    transition: height .2s;
    padding: 0px 4px;
}

.cioption {
    width: 100%;
    height: fit-content;

    margin-bottom: 60px;
}
.cioption:last-of-type {
    margin-bottom: 0px;
}

.cioption .ciheadercont {
    width: 100%;
    height: fit-content;

    margin-bottom: 40px;
}
.cioption .ciheadercont label {
    color: transparent;
    font-size: 25px;
    font-weight: 700;

    color: white;
}

.cioption #cisearchcont {
    width: 100%;
    height: fit-content;
}
.cioption #cisearchcont input {
    outline: none;
    
    width: 100%;
    height: 80px;

    color: white;
    font-size: 20px;
    font-weight: 500;

    background-color: #202020;
    border: 3px solid white;
    border-radius: 5px;

    padding: 0px 35px;
}


.cioption #getlinkbtn {
    cursor: pointer;

    width: 100%;
    height: 90px;

    color: white;
    font-size: 25px;
    font-weight: 700;

    background: linear-gradient(97.73deg, #001FC4 0%, #3F5EFD 100%);
    border: none;
    border-radius: 5px;

    /* border: 4px solid transparent; */
    /* margin-left: 4px; */
    outline: 4px solid transparent;

    transition: background .2s, outline .1s, border .2s, color .2s;
}
.cioption #getlinkbtn:hover {
    outline: 4px solid white;
}

.cioption #getlinkbtn.room {
    font-size: 18px;
    background: none;
    border: 4px solid #FFF500;

    outline: none;
}
.cioption #getlinkbtn.room:hover {
    color: black;
    background-color: #FFF500;
}

.cioption #getlinkbtn::selection {
    background-color: red;
    color: transparent;
    -webkit-text-stroke: 1px white;
}


#tttinvitecont #waiting {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: fit-content;

    color: white;

    font-size: 20px;
    font-weight: 700;

    margin-top: 30px;
}


