#sudoku {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: fit-content;
    min-height: 100vh;

    padding-top: 80px;
/* 
    background-color: white; */
}


#sudoku #control {
    display: flex;
    flex-direction: column;

    width: calc(100vh - 80px - 2 * 60px - 30px - 2 * ((100vh - 80px - 2 * 60px - 30px) / 6.75));
    /* width: 372px; */
    /* width: calc(110px * 3 + 10px * 2); */
    height: fit-content;
    /* height: calc(100vh - 80px - 2 * 60px - 30px); */

    margin-left: 30px;
}

#sudoku #control #newgamecont {
    position: relative;

    width: 100%;
    /* height: 80px; */
    height: calc((100vh - 80px - 2 * 60px - 30px) / 6.75);

    margin-bottom: 15px;
}
#sudoku #control #newgamecont #newgame {
    position: absolute;
    top: 0px;

    overflow: hidden;

    width: 100%;
    /* height: 80px; */
    height: calc((100vh - 80px - 2 * 60px - 30px) / 6.75);

    transition: height .15s;
}
#sudoku #control #newgamebtn {
    cursor: pointer;

    z-index: 10;
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    /* height: 80px; */
    height: calc((100vh - 80px - 2 * 60px - 30px) / 6.75);

    color: white;
    font-size: 17px;
    font-weight: 700;
    
    font-size: calc(((100vh - 80px - 2 * 60px - 30px) / 6.75) / 4.60);

    border: none;
    border-radius: 5px;
    /* border-radius: 5px 5px 0px 0px; */
    background: linear-gradient(97.73deg, #001FC4 0%, #3F5EFD 100%);

    transition: filter .1s;
}
#sudoku #control #newgamebtn:hover {
    filter: brightness(.9);
}

#sudoku #control #newgamemenu {
    z-index: 9;

    position: relative;
    /* position: absolute;
    top: -10px; */
    
    width: calc(100%);
    height: fit-content;

    /* box-shadow: 0px 0px 3px 0px #bebebe; */

    background: linear-gradient(97.73deg, #001FC4 0%, #3F5EFD 100%);
    border-radius: 7px;

    padding: 20px 30px;
    margin-top: 15px;

    transition: height .2s;
}
#sudoku #control #newgamemenu .menuoption {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: fit-content;

    border: 2.5px solid white;
    border-radius: 5px;

    padding: calc(((100vh - 80px - 2 * 60px - 30px) / 6.75) / 8) 0px;
    margin-top: 10px;

    color: white;
    font-size: 16px;
    font-weight: 600;

    font-size: calc(((100vh - 80px - 2 * 60px - 30px) / 6.75) / 5);

    transition: background-color .1s, color .1s, border .1s;
}
#sudoku #control #newgamemenu .menuoption:first-of-type {
    margin-top: 0px;
}
#sudoku #control #newgamemenu .menuoption:hover {
    background-color: white;
    color: #001FC4;
}
#sudoku #control #newgamemenu .menuoption:last-of-type {
    background-color: white;
    color: #001FC4;

    margin-top: 20px;
}
#sudoku #control #newgamemenu .menuoption:last-of-type:hover {
    background-color: black;
    color: white;
    border: 2.5px solid black;
}

#sudoku #control #newgamemenubottom {
    z-index: 9;

    display: none;

    position: relative;
    /* position: absolute;
    top: -10px; */
    
    width: calc(100%);
    height: fit-content;

    /* box-shadow: 0px 0px 3px 0px #bebebe; */

    background: linear-gradient(97.73deg, #001FC4 0%, #3F5EFD 100%);
    border-radius: 7px;

    padding: 20px 30px;
    margin-top: 15px;

    transition: height .2s;
}
#sudoku #control #newgamemenubottom .menuoption {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: fit-content;

    border: 2.5px solid white;
    border-radius: 5px;

    padding: calc(((100vh - 80px - 2 * 60px - 30px) / 6.75) / 8) 0px;
    margin-top: 10px;

    color: white;
    font-size: 16px;
    font-weight: 600;

    font-size: calc(((100vh - 80px - 2 * 60px - 30px) / 6.75) / 5);

    transition: background-color .1s, color .1s, border .1s;
}
#sudoku #control #newgamemenubottom .menuoption:first-of-type {
    margin-top: 0px;
}
#sudoku #control #newgamemenubottom .menuoption:hover {
    background-color: white;
    color: #001FC4;
}
#sudoku #control #newgamemenubottom .menuoption:last-of-type {
    background-color: white;
    color: #001FC4;

    margin-top: 20px;
}
#sudoku #control #newgamemenubottom .menuoption:last-of-type:hover {
    background-color: black;
    color: white;
    border: 2.5px solid black;
}


#sudoku #control #controlbtncont {
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    width: 100%;
    /* height: calc(540px - 80px - 110px * 3 - 10px * 2 - 15px * 2); */
    height: calc((100vh - 80px - 2 * 60px - 30px) / 6.75);

    color: white;
    font-size: 15px;
    font-weight: 600;

    /* border: 2px solid black; */
    border-bottom: 0px;
    
    /* background-color: #202020; */
    border-radius: 5px;

    transition: filter .1s;
}

#sudoku #control #controlbtncont .cbtn {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    flex-direction: column;

    width: 100%;
    height: 100%;

    color: #FFF500;

    background-color: #202020;
    border-radius: 5px;

    margin-right: 10px;

    transition: filter .1s;
}
#sudoku #control #controlbtncont .cbtn:last-of-type {
    margin-right: 0px;
}
#sudoku #control #controlbtncont .cbtn:hover {
    filter: brightness(.8);
}
#sudoku #control #controlbtncont .cbtn img {
    display: block;

    min-height: calc(((100vh - 80px - 2 * 60px - 30px) / 6.75) / 3.2);
}
#sudoku #control #controlbtncont .cbtn img:last-of-type {
    min-height: calc(((100vh - 80px - 2 * 60px - 30px) / 6.75) / 3.1);
}
#sudoku #control #controlbtncont .cbtn label {
    cursor: pointer;
    display: block;

    font-size: 13px;
    font-weight: 600;

    font-size: calc(((100vh - 80px - 2 * 60px - 30px) / 6.75) / 6.15);

    margin-top: 5px;
}


#sudoku #control #btncont {
    display: flex;
    flex-wrap: wrap;

    width: fit-content;
    height: fit-content;

    margin-top: 15px;
}

#sudoku #control #btncont .btn {
    cursor: pointer;

    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    /* width: 110px;
    height: 110px; */

    width: calc((100vh - 80px - 2 * 60px - 30px - 2 * ((100vh - 80px - 2 * 60px - 30px) / 6.75) - 20px) / 3);
    height: calc((100vh - 80px - 2 * 60px - 30px - 2 * ((100vh - 80px - 2 * 60px - 30px) / 6.75) - 20px) / 3);

    color: white;
    font-size: 25px;
    font-weight: 600;

    font-size: calc(((100vh - 80px - 2 * 60px - 30px - 2 * ((100vh - 80px - 2 * 60px - 30px) / 6.75) - 20px) / 3) / 4.68);

    margin-right: 10px;
    margin-bottom: 10px;

    background-color: #202020;
    border-radius: 5px;

    outline: 3px solid transparent;

    transition: filter .1s, outline .2s;
}
#sudoku #control #btncont .btn:hover {
    /* background-color: rgb(50, 50, 50); */
    filter: brightness(.8);
    /* outline: 3px solid white; */
}
#sudoku #control #btncont .btn:nth-of-type(3n) {
    margin-right: 0px;
}
#sudoku #control #btncont .btn:nth-of-type(7), #sudoku #control #btncont .btn:nth-of-type(8), #sudoku #control #btncont .btn:nth-of-type(9) {
    margin-bottom: 0px;
}

#sudoku #control #btncont .btn div {
    position: absolute;
    bottom: 5px;
    right: 10px;

    font-size: 15px;

    font-size: calc(((100vh - 80px - 2 * 60px - 30px - 2 * ((100vh - 80px - 2 * 60px - 30px) / 6.75) - 20px) / 3) / 7.8);
}

#sudoku #btncont #erasebtn {
    display: none !important;
}


#sudoku #control #controlbtncontbottom {
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    width: 100%;
    /* height: calc(540px - 80px - 110px * 3 - 10px * 2 - 15px * 2); */
    height: calc((100vh - 80px - 2 * 60px - 30px) / 6.75 - 20px);

    color: white;
    font-size: 15px;
    font-weight: 600;

    /* border: 2px solid black; */
    border-bottom: 0px;
    
    /* background-color: #202020; */
    border-radius: 5px;

    display: none;

    transition: filter .1s;
}

#sudoku #control #controlbtncontbottom .cbtn {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    /* flex-direction: column; */

    width: 100%;
    height: 100%;

    color: #FFF500;

    background-color: #202020;
    border-radius: 5px;

    margin-right: 10px;

    transition: filter .1s;
}
#sudoku #control #controlbtncontbottom .cbtn:last-of-type {
    margin-right: 0px;
}
#sudoku #control #controlbtncontbottom .cbtn:hover {
    filter: brightness(.8);
}
#sudoku #control #controlbtncontbottom .cbtn img {
    display: block;

    margin-right: 15px;

    height: 18px !important;
    /* min-height: calc(((100vh - 80px - 2 * 60px - 30px) / 6.75) / 3.2); */
}
#sudoku #control #controlbtncontbottom .cbtn img:last-of-type {
    min-height: calc(((100vh - 80px - 2 * 60px - 30px) / 6.75) / 3.1);
}
#sudoku #control #controlbtncontbottom .cbtn label {
    cursor: pointer;
    display: block;

    font-size: 13px;
    font-weight: 600;

    font-size: calc(((100vh - 80px - 2 * 60px - 30px) / 6.75) / 6.15);
}


#sudoku.mid {
    flex-direction: column;

    padding-top: 120px;
    padding-bottom: 30px;
}
#sudoku.mid .square {
    /* font-size: max(calc(((100vw - 50px) / 9) / 2)); */
}
