}
#sudoku.mid #control #btncont .btn:nth-last-of-type(5) {
    margin-bottom: 0px !important;
}
#sudoku.mid #btncont #erasebtn {
    display: flex !important;
}
#sudoku.mid #control #newgamemenubottom {
    display: block;
