#gamecontrolcont {
    width: fit-content;
    height: fit-content;

    margin-right: 100px;
}

#gamecontrolcont #modecont {
    overflow: hidden;

    width: calc(100vw - 20px);
    max-width: 450px;
    height: 80px;

    /* border: 2px solid white; */
    background-color: white;
    border-radius: 3px;

    transition: height .2s, opacity .2s;
}
#gamecontrolcont #modecont #modeshowcase {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    width: 100%;
    height: 80px;
}
#gamecontrolcont #modecont #modeimg {
    display: flex;
    justify-content: center;
    align-items: center;

    width: fit-content;
    height: 100%;

    padding: 0px 20px;
}
#gamecontrolcont #modecont #modeimg img {

}
#gamecontrolcont #modecont #modetime {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;
}
#gamecontrolcont #modecont #modetime label {
    color: black;

    font-size: 23px;
    font-weight: 700;

}
#gamecontrolcont #modecont #modedrop {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: fit-content;
    height: 100%;

    padding: 0px 25px;
}
#gamecontrolcont #modecont #modedrop img {
    transition: transform .2s;
}
#gamecontrolcont #modecont #modedrop:hover {
    background-color: #f5f5f5;
}

#gamecontrolcont #modecont #modeoptioncont {
    width: 100%;
    height: 220px;

    margin-top: 16px;
    padding-right: 25px;
}
#gamecontrolcont #modecont #modeoptioncont .moption {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    width: 100%;
    height: 50px;

    margin-bottom: 20px;
}
#gamecontrolcont #modecont #modeoptioncont .moption .moimg {
    display: flex;
    justify-content: center;
    align-items: center;

    min-width: 65px;
    height: 100%;

    margin-right: 10px;
}
#gamecontrolcont #modecont #modeoptioncont .moption .moimg img {
    max-width: 30px;
    max-height: 30px;
}
#gamecontrolcont #modecont #modeoptioncont .moption .mocont {
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    width: 100%;
    height: 100%;
}
#gamecontrolcont #modecont #modeoptioncont .moption .mocont .mooption {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    background-color: transparent;
    border: 3px solid black;
    border-radius: 3px;

    margin-right: 10px;

    transition: outline .1s, background-color .1s, border-color .1s;
}
#gamecontrolcont #modecont #modeoptioncont .moption .mocont .mooption:last-child {
    margin-right: 0px;
}
#gamecontrolcont #modecont #modeoptioncont .moption .mocont .mooption label {
    cursor: pointer;

    color: black;
    font-size: 15px;
    font-weight: 600;

    transition: color .1s;
}
#gamecontrolcont #modecont #modeoptioncont .moption .mocont .mooption:hover {
    border-color: #0029FF;
}
#gamecontrolcont #modecont #modeoptioncont .moption .mocont .mooption:hover label {
    color: #0029FF;
}

#gamecontrolcont #modecont #modeoptioncont .moption .mocont .mooption.active:hover {
    border-color: black;
}
#gamecontrolcont #modecont #modeoptioncont .moption .mocont .mooption.active {
    background-color: black;
}
#gamecontrolcont #modecont #modeoptioncont .moption .mocont .mooption.active label {
    color: white;
}

#gamecontrolcont #modecont.disabled {
    opacity: .4;
}
#gamecontrolcont #modecont.disabled #modedrop {
    cursor: not-allowed;
}
#gamecontrolcont #modecont.disabled #modedrop:hover {
    background-color: white;
}


#gamecontrolcont #colorcont {
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    width: calc(100vw - 20px);
    max-width: 450px;
    height: 80px;

    margin: 20px 0px;
}
#gamecontrolcont #colorcont .colorbtn {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    border: 3px solid white;
    border-radius: 5px;

    transition: border .1s, background-color .1s;
}
#gamecontrolcont #colorcont .colorbtn label {
    cursor: pointer;

    color: white;

    font-size: 20px;
    font-weight: 700;

    user-select: none;

    transition: color .1s;
}
#gamecontrolcont #colorcont .colorbtn:hover label {
    color: #FFF500;
}
#gamecontrolcont #colorcont #whitebtn {
    margin-right: 15px;
}
#gamecontrolcont #colorcont #blackbtn {
    margin-left: 15px;
}

#gamecontrolcont #colorcont .colorbtn.active {
    background-color: #FFF500;
    border: 3px solid #FFF500;
}
#gamecontrolcont #colorcont .colorbtn.active label {
    color: black;
    font-weight: 700;
}
#gamecontrolcont #colorcont .colorbtn.active:hover label {
    color: black;
}


#gamecontrolcont #playbutton {
    cursor: not-allowed;

    display: block;

    width: calc(100vw - 20px);
    max-width: 450px;
    height: 90px;

    color: white;
    font-size: 25px;
    font-weight: 700;

    background: linear-gradient(97.73deg, #001FC4 0%, #3F5EFD 100%);
    border-radius: 7px;
    border: none;

    opacity: .3;

    outline: 4px solid transparent;
    transition: background-color .2s, opacity .2s, outline .1s;
}
#gamecontrolcont #playbutton.ready {
    cursor: pointer;
    opacity: 1;
}
#gamecontrolcont #playbutton.ready:hover {
    outline: 4px solid white;
}


