#drawspot {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    /* flex-direction: column; */

    width: fit-content;
    height: 100%;

    /* background-color: black; */
}

#usebtn {
    cursor: pointer;

    position: absolute;
    top: -70px;
    left: 0px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 40px;
    height: 40px;

    border: 3px solid black;
    border-radius: 5px;
    background-color: white;

    transform: scale(0);

    transition: background-color .1s, transform .2s;
}
#usebtn:hover {
    background-color: #FFF000;
}
#usebtn.ready {
    transform: scale(1);
}
#usebtn.active {
    background-color: #FFF000;
}

#drawspot .dtoolbar {
    position: relative;

    display: flex;
    flex-direction: column;

    /* overflow: hidden; */

    width: 40px;
    height: 100%;

    min-width: 40px;

    background-color: white;
    outline: 3px solid black;
    border-radius: 5px;

    margin-left: 40px;
}
#drawspot .dtoolbar:first-of-type {
    margin-right: 40px;
    margin-left: 0px;
}
#drawspot .dtoolbar:last-of-type {
    /* margin-left: 40px !important; */
}

#drawspot .dtoolbar .dtboption {
    cursor: pointer;

    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 40px;

    border-bottom: 3px solid black;
}
#drawspot .dtoolbar .dtboption:first-of-type {
    border-radius: 5px 5px 0px 0px;
}
#drawspot .dtoolbar .dtboption:last-of-type {
    border-bottom: none;
    border-radius: 0px 0px 5px 5px;
}
#drawspot .dtoolbar .dtboption:hover {
    background-color: #FFF000;
}

#drawspot .dtoolbar .dtboption.active {
    background-color: #FFF000;
}
#drawspot .dtoolbar .dtboption.popup {
    background-color: #FFF000;
}

#drawspot .dtoolbar .dtboption .dtbothick {
    text-align: center;
}
#drawspot .dtoolbar .dtboption .dtbothick label {
    cursor: pointer;

    display: block;
    font-size: 13px;
}
#drawspot .dtoolbar .dtboption .dtbothick label span {
    font-size: 10px;
}

#drawspot .dtoolbar .dtboption svg {
    display: block;

    margin-top: 3px;
}
#drawspot .dtoolbar .dtboption.show {
    cursor: default;
}
#drawspot .dtoolbar .dtboption.show:hover {
    background-color: white;
}

#drawspot .dtoolbar .dtboption .dtboicon {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;
}
#drawspot .dtoolbar .dtboption .dtbocolor {
    width: 20px;
    height: 20px;

    border: 3px solid black;
    border-radius: 50%;
}

#drawspot .dtoolbar .dtboption .popup {
    z-index: 5;

    cursor: default;

    position: absolute;
    top: 50%;
    transform: translate(0px, -50%);

    display: none;

    width: fit-content;
    height: fit-content;

    background-color: white;
    outline: 3px solid black;
    border-radius: 5px;

    padding: 5px 5px;
}
#drawspot .dtoolbar .dtboption .popup.right {
    left: calc(100% + 15px);
}
#drawspot .dtoolbar .dtboption .popup.left {
    right: calc(100% + 15px);
}

#drawspot .dtoolbar .dtboption .popup .putop {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: fit-content;

    /* background-color: black; */
}
#drawspot .dtoolbar .dtboption .popup .pubottom {
    width: 100%;
    height: fit-content;

    margin-top: 5px;
}

#drawspot .dtoolbar .dtboption .popup .pucolor {
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 30px;
    height: 30px;

    border-radius: 50%;
    border: 3px solid black;

    margin-right: 5px;
}
#drawspot .dtoolbar .dtboption .popup .pucolor:last-of-type {
    margin-right: 0px;
}
#drawspot .dtoolbar .dtboption .popup .pucolor:hover {
    filter: brightness(.8);
}

#drawspot .dtoolbar .dtboption .popup .puinput {
    position: relative;

    overflow: hidden;

    width: fit-content;
    height: fit-content;
}
#drawspot .dtoolbar .dtboption .popup input {
    width: 100%;
    height: 40px;

    border: 3px solid black;
    border-radius: 5px;

    background-color: #F5F5F5;

    padding: 0px 10px;
    padding-right: 35px;
}
#drawspot .dtoolbar .dtboption .popup .puinputbtn {
    position: absolute;
    right: 0px;

    width: fit-content;
    height: 100%;

    background-color: transparent;
    border: none;
}

#drawspot .dtoolbar .dtboption .popup .puthick {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 150px;
    height: fit-content;


}
#drawspot .dtoolbar .dtboption .popup .puthick input {
    padding: 0px 10px;
}
#drawspot .dtoolbar .dtboption .popup .puthick button {
    cursor: pointer;

    width: 70px;
    height: 40px;

    margin-left: 10px;

    color: black;
    background-color: white;
    border: 3px solid black;
    border-radius: 5px;

    transition: color .1s, background-color .1s;
}
#drawspot .dtoolbar .dtboption .popup .puthick button:hover {
    background-color: #FFF000;
}

