#lpcont {
    width: 100%;
    height: fit-content;

    background-repeat: no-repeat;
    background-size: 100vw;
    background-position-x: center;
    background-position-y: 110px;

    padding-top: 80px;
}

#lp {
    display: flex;
    justify-content: center;
    align-items: center;

    flex-direction: column;

    width: 100%;
    height: calc(100vh - 80px);

    text-align: center;

    /* padding: 0px 200px;
    padding-right: 150px; */
}

#lp h1 {
    color: #FFF500;

    font-size: 70px;
    font-weight: 900;

    letter-spacing: .03em;
    margin-right: -.03em;

    margin: 0px;
    margin-bottom: 70px;
}
#lp h2 {
    color: white;

    font-size: 60px;
    font-weight: 900;

    letter-spacing: .03em;
    margin-right: -.03em;

    margin: 0px;
}

#lp h2 span {
    color: transparent;

    font-size: 60px;
    font-weight: 900;
}
#lp h2 span.lpw {
    -webkit-text-stroke: white 2px;
}
#lp h2 span.lpb {
    -webkit-text-stroke: #0029FF 3px;
}

