#gameshowcase {
    display: flex;
    flex-direction: column;

    width: 100%;
    height: calc(100vh - 80px);

    padding: 0px 100px;
    padding-top: 50px;
    padding-right: 0px;
}
#gameshowcase h1 {
    color: #FFF500;
    font-size: 35px;
}

#gameshowcase #gamescont {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    width: fit-content;
    height: 100%;

    margin-top: 0px;
}

#gameshowcase #gamescont .game {
    cursor: pointer;

    position: relative;

    width: 300px;
    height: 360px;

    background-repeat: no-repeat;
    background-size: cover;
    background-position: left;
    /* background: linear-gradient(270deg, rgba(0, 0, 0, 0) 42.71%, #000000 100%);
    background-color: #292929; */

    border-radius: 5px;
    margin-left: 40px;

    margin-right: 50px;
}
#gameshowcase #gamescont .game:first-of-type {
    margin-left: 0px;
}

#gameshowcase #gamescont .game .gamename {
    position: absolute;
    top: 50%;
    left: -40px;
    transform: translate(0px, -50%);

    color: white;
    font-size: 30px;
}


#vidshowcase {
    width: 100%;
    height: fit-content;

    padding: 0px 50px;

    margin-bottom: 100px;
}
#vidshowcase #vid {
    width: 100%;
    height: calc(650px);

    background-color: #202020;
}


