#chess {
    width: 100%;
    height: 100vh;
    padding-top: 80px;
}



#chess.mid #chessplay {
    flex-direction: column;
    padding-top: 20px;
}
#chess.mid #chessplay #chessboard {
    margin: 20px 0px;
}

#chess.mid #chessplay #cpleft {
    flex-direction: column;
}
#chess.mid #cpleft .player {
    display: flex;
}

#chess.mid #cpright .player {
    display: none;
}
#chess.mid #cpright {
    width: calc(100vh - 160px);
    max-width: calc(100vw - 100px);

    margin-left: 0px;
    /* padding: 0px 45px; */
    /* height: 100%; */
}

#chess.mid #ccbottom {
    padding-left: 0px;
}

#chess.mobile #chessplay {
    flex-direction: column;
    padding-top: 20px;

    font-size: 13px;
}
#chess.mobile #chessplay #chessboard {
    margin: 20px 0px;

    min-width: calc(100vw - 40px);
    min-height: calc(100vw - 40px);
}

#chess.mobile #chessboard .square {
    width: calc((100vw - 40px) / 8) !important;
    height: calc((100vw - 40px) / 8) !important;

    max-width: calc((100vw - 40px) / 8) !important;
    max-height: calc((100vw - 40px) / 8) !important;
}

#chess.mobile #chessplay #cpleft {
    flex-direction: column;

    width: 100%;
    padding: 0px 20px;
}
#chess.mobile #cpleft .player {
    display: flex;
    
    font-size: 12px;

    height: 50px;
}
#chess.mobile .player .time {
    min-width: 100px;
    padding: 10px 20px !important;
}

#chess.mobile #cpright .player {
    display: none;
}
#chess.mobile #cpright {
    width: 100%;
    /* max-width: calc(100vw - 100px); */

    margin-left: 0px;
    padding: 0px 20px;
    /* padding: 0px 45px; */
    /* height: 100%; */
}
